.popup__add-card {
    font-family: 'Inter', Arial, sans-serif;
    z-index: 2;
    background-color: rgba(0, 0,0, .5);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    visibility: hidden;
    opacity: 0;
    transition: visibility 0s, opacity 0.5s linear;
}